<template>
    <div class="lineup_group">
        <div class="group_container">

            <div class="group_box" ref="group_box"
            @touchstart="$refs.group_box[gId].classList.add('touch')"
            @touchend="$refs.group_box[gId].classList.remove('touch')"
            v-for="g,gId in $store.battle.lineupGroups" :key="g.index"
            @click="SelectThisGroup(g)">
                <div class="lineup_warrior_post" v-if="g.lineupList.length > 0">
                    <img :src="g.lineupList[g.lineupList.length-1].list[0].img.post" class="post_img" alt="">
                    <div class="mask cover"></div>
                </div>
                <div class="g_name">{{ g.groupName }}</div>
                <div class="g_describe">{{ g.groupDescribe==""?"无描述": g.groupDescribe}}</div>
                <div class="g_color" :style="`background-color:${g.groupColor==''?'black':g.groupColor};`"></div>
            </div>


        </div>
        <div class="bottom">
            <div class="add_new_group_btn" ref="add_new_group_btn"
            @touchstart="$refs.add_new_group_btn.classList.add('click')"
            @touchend="$refs.add_new_group_btn.classList.remove('click')"
            @click="AddNewGroup()">新增分组</div>
        </div>

        <div class="group_panel" v-if="groupPanel.display">
            <div class="mask cover" @click="CloseGroupPanel()"></div>

            <div class="group_panel_container" v-if="currentGroup">
                <div class="base_info">
                    <div class="group_name">
                        <div class="title">分组名称</div>
                        <input type="text" placeholder="请输入分组名称" v-model="currentGroup.groupName" class="value">
                    </div>
                    <div class="group_describe">
                        <div class="title">分组描述</div>
                        <input type="text" placeholder="描述该分组" v-model="currentGroup.groupDescribe" class="value">
                    </div>
                    <div class="group_members">
                        <div class="title">组内阵容</div>
                        <div class="lineup_member">
                            <div class="lineup_preview_box" v-for="m,mIndx in currentGroup.lineupList.slice(0, 8)" :key="m.index">
                                <div class="lineup_name">{{ m.lineupName==""?"未命名": m.lineupName}}</div>
                                <div class="warrior_list">
                                    <div class="warrior_head" v-for="w in m.list" :key="w.index">
                                        <img :src="w.img.head" class="head_img" alt="">
                                    </div>
                                </div>
                                
                            </div>
                            <div class="andsoon_tip" v-if="currentGroup.lineupList.length > 8">··· 等 <span>{{ currentGroup.lineupList.length }}</span> 支队伍阵容 ···</div>
                        </div>
                        
                    </div>
                </div>

                <div class="import_group" ref="import_group"
                @touchstart="$refs.import_group.classList.add('click')"
                @touchend="$refs.import_group.classList.remove('click')"
                @click="ImportGroupTeam()">
                    导入分组
                </div>
                <div class="delete_btn" ref="delete_btn"
                @touchstart="$refs.delete_btn.classList.add('click')"
                @touchend="$refs.delete_btn.classList.remove('click')"
                @click="DeleteGroup()">删除分组</div>

                <div class="clear_lineup_btn" ref="clear_lineup_btn"
                @touchstart="$refs.clear_lineup_btn.classList.add('click')"
                @touchend="$refs.clear_lineup_btn.classList.remove('click')"
                @click="ClearGroupLineups()">清除组内阵容</div>

                <TopicTitle title="分组" :titleFontSize="4.5" :closeFunc="CloseGroupPanel"/>
            </div>

        </div>
        
        <div class="group_add_panel" v-if="groupCreatePanel.display">
            <div class="mask cover" @click="CloseGroupPanel()"></div>
            <div class="group_create_panel_container">
                <div class="optional">
                    <div class="group_name">
                        <div class="title">分组名称</div>
                        <input type="text" placeholder="请输入分组名称" v-model="groupData.groupName" class="value">
                    </div>
                    <div class="group_describe">
                        <div class="title">分组描述</div>
                        <input type="text" placeholder="描述该分组" v-model="groupData.groupDescribe" class="value">
                    </div>
                    <div class="group_color_select">
                        <div class="top">
                            <div class="title">颜色选择</div>
                            <div class="tip">选择一种颜色来直观区别组别</div>
                        </div>
                        <div class="color_bar">
                            <div class="color_item" ref="color_item"
                            @touchstart="$refs.color_item[cIndex].classList.add('click')"
                            @touchend="$refs.color_item[cIndex].classList.remove('click')"
                            v-for="c,cIndex in colorList" :key="c.index"
                            @click="groupData.groupColor = c">
                                <div class="color_circle" :style="`background-color:${c};`"></div>
                                <div class="current_frame" v-if="c == groupData.groupColor"></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="add_btn" ref="add_btn"
                @touchstart="$refs.add_btn.classList.add('click')"
                @touchend="$refs.add_btn.classList.remove('click')"
                @click="ConfirmAdd()">确认新建</div>
                <div class="return_def_btn" ref="return_def_btn"
                @touchstart="$refs.return_def_btn.classList.add('click')"
                @touchend="$refs.return_def_btn.classList.remove('click')"
                @click="ReturnDefConfig()">恢复默认</div>

                <TopicTitle title="新建分组" :titleFontSize="5.5" :closeFunc="CloseGroupPanel"/>
            </div>
        </div>

        
        <TopNav title="阵容分组" />
    </div>
</template>


<script>
import TopNav from '@/components/container/TopNav.vue';
import TopicTitle from '@/components/style/TopicTitle.vue';
import { BattleUtil } from '@/util/battle';
import Save from '@/util/save';
export default {
    name : 'LineupGroup',
    components:{
        TopNav,
        TopicTitle
    },
    data(){
        return{
            groupPanel:{
                display:false,
            },
            groupCreatePanel:{
                display : false
            },

            groupData:{
                groupName : "",
                groupDescribe : "",
                groupColor : "cyan"
            },

            colorList: [
                // 红色系
                "red",            // 鲜红色
                "tomato",         // 番茄红（偏橙）
                "coral",          // 珊瑚橙红
                "lightcoral",     // 浅珊瑚红

                // 粉色系
                "hotpink",        // 亮粉红色
                "fuchsia",        // 亮粉紫
                "orchid",         // 兰花紫（偏粉）

                // 紫色系
                "violet",         // 紫罗兰色
                "thistle",       // 蓟色（浅紫色，带一点灰调但明亮）
                "magenta",        // 亮品红（紫粉调）

                // 黄色系
                "yellow",         // 纯黄色
                "gold",           // 金色
                "khaki",          // 卡其黄（浅棕黄）
                "lemonchiffon",   // 柠檬纱色（极浅黄）
                "moccasin",       // 鹿皮色（浅棕黄）
                "orange",         // 鲜橙色（黄橙调）

                // 绿色系
                "lime",           // 亮黄绿色
                "chartreuse",     // 黄绿（比lime更偏黄）
                "limegreen",      // 酸橙绿（深于lime）
                "palegreen",      // 淡绿色（柔和亮绿）

                // 蓝色系
                "cyan",           // 亮青色
                "aquamarine",     // 海宝蓝色
                "turquoise",      // 绿松石色（青蓝）
                "royalblue",      // 宝蓝色
                "lightskyblue",   // 浅天蓝色
                "powderblue",      // 粉蓝色（浅蓝带灰）
                
                "white"

            ],

            currentGroup:null,
            // 用于存储定时器ID
            saveTimer:null,

            autoSaveTime:1000,
        }
    },
    watch:{
        // 监听深层属性
        'currentGroup.groupName': {
            handler(newVal, oldVal) {
                clearTimeout(this.saveTimer)
                this.saveTimer = setTimeout(() => {
                    this.SaveData(this.currentGroup);
                }, this.autoSaveTime);
            },
            deep: true // 开启深层监听
        },
        'currentGroup.groupDescribe': {
            handler(newVal, oldVal) {
                clearTimeout(this.saveTimer)
                this.saveTimer = setTimeout(() => {
                    this.SaveData(this.currentGroup);
                }, this.autoSaveTime);
            },
            deep: true // 开启深层监听
        }
    },
    methods:{
        SaveData(whichGroup){
            for(let i in this.$store.battleStorage.lineupGroups){
                if(this.$store.battleStorage.lineupGroups[i].gid == whichGroup.gid){
                    this.$store.battleStorage.lineupGroups[i].groupName = whichGroup.groupName
                    this.$store.battleStorage.lineupGroups[i].groupDescribe = whichGroup.groupDescribe
                }
            }
            let s = new Save(this.$store)
            s.SaveBattleData()
        },
        //选中组
        SelectThisGroup(group){
            this.groupPanel.display = true;
            this.currentGroup = group
        },
        
        //恢复默认
        ReturnDefConfig(){
            this.groupData = {
                groupName : "默认组",
                groupDescribe : "",
                groupColor : "cyan"
            }
        },
        AddNewGroup(){
            this.groupCreatePanel.display = true;
        },
        //确认新建分组
        ConfirmAdd(){
            if(this.groupData.groupName == ""){
                this.$store.Alert('分组名称不能为空!');
                return;
            }
            let that = this;
            let f = function(){
                let b = new BattleUtil(that.$store);
                let res = b.CreateNewGroup(that.groupData);
                if(res){
                    that.$store.Alert('分组创建成功');
                    that.CloseGroupPanel();
                }
            }
            for(let i in this.$store.battle.lineupGroups){
                if(this.$store.battle.lineupGroups[i].groupName == this.groupData.groupName){
                    this.$store.Alert('分组名称已存在，请重新命名!');
                    return;
                }
                else if(this.$store.battle.lineupGroups[i].groupColor == this.groupData.groupColor){
                    
                    let params = {
                        msg : `该分组颜色已被【${this.$store.battle.lineupGroups[i].groupName}】分组所选择，还需要继续使用该颜色吗？`,
                        CallBack : f,
                    }
                    this.$store.Prompt(params)
                    return;
                }
            }
            f();
        },
        //清除组内阵容
        ClearGroupLineups(){
            let that = this;
            let f = function(){
                let b = new BattleUtil(that.$store);
                let res = b.ClearGroupLineups(that.currentGroup.gid);
                if(res){
                    that.currentGroup.lineupList = [];
                    // that.CloseGroupPanel();
                    that.$store.Alert('清除成功');
                }
                else{
                    that.$store.Alert('清除失败');
                }
            }
            let params = {
                msg : `确定将该分组下的所有阵容信息清除？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        //删除分组
        DeleteGroup(){
            let that = this;
            let f = function(){
                let b = new BattleUtil(that.$store);
                let res = b.DeleteGroup(that.currentGroup.gid);
                if(res){
                    that.CloseGroupPanel();
                    that.$store.Alert('删除成功');
                }
                else{
                    that.$store.Alert('删除失败');
                }
            }
            let params = {
                msg : `确定删除该分组吗？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        //导入分组队伍
        ImportGroupTeam(){
            let that = this;

            let f = function(){
                let arr = [];
                //把组内队伍整合
                for(let i in that.$store.battle.lineupList){
                    if(that.$store.battle.lineupList[i].groupIds.includes(that.currentGroup.gid)){
                        arr.push(that.$store.battle.lineupList[i]);
                    }
                }
                that.$store.battle.visibleLineups =  arr;
                that.$store.battle.currentGroup = that.currentGroup;
                that.$store.Alert('导入成功');
                that.$router.back();
            }

            let params = {
                msg : `确定将阵容列表导入为该分组下的阵容列表吗？`,
                CallBack : f,
            }
            this.$store.Prompt(params)

        },
        CloseGroupPanel(){
            this.groupPanel.display = false;
            this.groupCreatePanel.display = false;
        }
    }
}

</script>


<style lang="less" scoped>
.lineup_group{
    position: absolute;left: 0; top: 0;
    width: 100vw;
    height: 100vh;
    background: url('../../assets/background/bg (1).jpg')right bottom no-repeat;
    background-size: cover;
    .group_container{
        top: 12vh; left: 5vw;
        width: 90vw; height: 76vh;
        padding: 2vh 0;
        // background: #000;
        overflow-x: hidden;
        overflow-y: scroll;
        .group_box{
            position: relative; float: left;
            width: 40vw; height: 30vw;
            background: rgba(0, 0, 0, 0.4);
            margin: 2vw 0 0 3.3vw;
            transition: all .1s;
            .lineup_warrior_post{
                left: 0; top: 0;
                width: 40vw; height: 30vw;
                overflow: hidden;
                .post_img{
                    left: 0; top: 50%;
                    width: 100%; 
                    filter: blur(2px);
                    transform: translate(0,-30%);
                }
                .mask{
                    transition: all .1s;
                    background: radial-gradient(
                        circle at center,    
                        transparent 30%,
                        rgb(0, 0, 0) 
                    );
                }
            }
            .g_name{
                left: 5vw; top: 4vw;
                width: 30vw; height: 5vw;
                // display: flex;
                // align-items: center;
                line-height: 5vw;
                text-indent: 1vw;
                text-align: left;
                background: rgba(0, 0, 0, 0.65);
                font-size: 2.6vw;
            }
            .g_describe{
                left: 5vw; top: 11vw;
                width: 30vw; height: 15vw;
                text-align: left;
                text-indent: 1vw;
                background: rgba(0, 0, 0, 0.65);
                color: rgb(224, 224, 224);
                font-size: 2.6vw;
            }
            .g_color{
                left: 0; top: 0;
                width: 100%; height: 1.5vw;
                opacity: .8;
            }
            &.touch{
                background: rgba(255, 255, 255, 0.1);
                transform: scale(.9);
                .lineup_warrior_post{
                    left: 0; top: 0;
                    width: 40vw; height: 30vw;
                    overflow: hidden;
                    .post_img{
                        left: 0; top: 50%;
                        width: 100%; 
                        filter: blur(0);
                        transform: translate(0,-30%);
                    }
                    .mask{
                        
                        background: radial-gradient(
                            circle at center,    
                            transparent 60%,
                            rgb(0, 0, 0) 
                        );
                    }
                }
            }
        }
    }
    .bottom{
        position: absolute; left: 5vw; bottom: 3vh; top: auto;
        width: 90vw; height: 5vw;
        .add_new_group_btn{
            left: auto; right: 0;
            width: 20vw; height: 5vw;
            line-height: 5vw;
            font-weight: bold;
            color: black;
            background: white;
        }
    }
    .group_add_panel{
        position: absolute;left: 0; top: 0;
        width: 100vw;
        height: 100vh;
        z-index: 1;
        .mask{
            background: rgba(0, 0, 0, 0.5);
        }
        .group_create_panel_container{
            left: 0; top: 50%;
            width: 100vw; height: 90vw;
            margin: -45vw 0 0 0;
            background: url('../../assets/background/bg (7).png')#393b3e bottom right no-repeat;
            background-size: cover;
            .optional{
                left: 5vw; top: 20vw;
                width: 90vw; height: 50vw;
                // background: red;
                .group_name,.group_describe{
                    position: relative; float: left;
                    width: 90vw; height: 7vw;
                    background: rgba(0, 0, 0, 0.4);
                    margin: 0 0 3vw 0;
                    .title{
                        position: relative;float: left;
                        width: 20vw; height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                    .value{
                        position: relative;float: left;
                        width: 65vw; height: 5vw;
                        margin: 1vw 0 0 0;
                        border: 0;
                        display: flex;
                        align-items: center;
                        text-align: left;
                        justify-content: left;
                        background: transparent;
                        color: white;
                        &:focus{
                            box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.2);
                            outline: none;
                        }
                    }
                }
                .group_color_select{
                    position: relative; float: left;
                    width: 90vw; 
                    background: rgba(0, 0, 0, 0.3);
                    .top{
                        position: relative;float: left;
                        width: 90vw; height: 7vw;
                        .title{
                            position: relative;float: left;
                            width: 20vw; height: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }
                        .tip{
                            position: relative;float: left;
                            width: 60vw; height: 100%;
                            display: flex;
                            align-items: center;
                            text-align: left;
                            color: gray;
                        }
                    }
                    .color_bar{
                        position: relative; float: left;
                        width: 90vw; height: 27vw;
                        .color_item{
                            position: relative; float: left;
                            width: 7vw; height: 7vw;
                            margin: .5vw 0 1vw 2.7vw;
                            background: #000000;
                            transition: all .1s;
                            border-radius: 50%;
                            .color_circle{
                                left: .5vw; top: .5vw;
                                width: 6vw; height: 6vw;
                                border-radius: 50%;
                            }
                            .current_frame{
                                position: absolute;
                                left: 0; top: 0;
                                width: 7vw; height: 7vw;
                                background: url('../../assets/ui/ui\ \(198\).png')center no-repeat;
                                background-size: 100% 100%;
                                border-radius: 50%;
                                // border:1vw  1vw yellow ;
                                box-shadow: 0 0 1vw rgb(255, 255, 255);
                            }
                        }
                    }
                }
            }
            .add_btn,.return_def_btn{
                position: absolute; right: 5vw; bottom: 5vw; left: auto;
                width: 20vw; height: 5vw;
                display: flex;
                align-items: center;
                justify-content: center;
                color: black;
                font-weight: bold;
                background: white;
            }
            .return_def_btn{
                right: 30vw;
            }
        }
    }
    .group_panel{
        position: absolute;left: 0; top: 0;
        width: 100vw;
        height: 100vh;
        z-index: 1;
        .mask{
            background: rgba(0, 0, 0, 0.5);
        }
        .group_panel_container{
            left: 0; top: 50%;
            width: 100vw; height: 80vw;
            margin: -40vw 0 0 0;
            background: url('../../assets/background/bg (7).png')#393b3e bottom right no-repeat;
            background-size: cover;
            // background: red;
            .base_info{
                left: 5vw; top: 18vw;
                width: 90vw; height: 34vw;
                .group_name,.group_describe,.group_members{
                    position: relative; float: left;
                    width: 90vw; height: 7vw;
                    background: rgba(0, 0, 0, 0.3);
                    margin: 0 0 3vw 0;
                    .title{
                        position: absolute;
                        left: 0; top: 0;
                        width: 20vw; height: 7vw;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                    .value{
                        position: absolute;
                        left: 20vw; top: 1vw;
                        width: 65vw; height: 5vw;
                        border: 0;
                        display: flex;
                        align-items: center;
                        text-align: left;
                        justify-content: left;
                        background: transparent;
                        color: white;
                        &:focus{
                            box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.2);
                            outline: none;
                        }
                    }
                    .lineup_member{
                        position: absolute;
                        left: 20vw; top: 1vw;
                        width: 70vw; height: 24vw;
                        overflow: scroll;
                        // background: #000;
                        .lineup_preview_box,.andsoon_tip{
                            position: relative; float: left;
                            width: 35vw; height: 6vw;
                            .lineup_name{
                                left: 17vw; top: 0;
                                width: 20vw; height:  6vw;
                                line-height:  6vw;
                                font-size: 2.4vw;
                                text-align: left;
                                color: rgb(195, 195, 195);
                            }
                            .warrior_list{
                                position: absolute;
                                left: 0;top: .5vw;
                                width: 17vw; height: 5vw;
                                .warrior_head{
                                    position: relative; float: left;
                                    width: 5vw; height: 5vw;
                                    margin: 0 0 0 .5vw;
                                    background: #202020;
                                    // border-radius: 50%;
                                    .head_img{
                                        left: .5vw; top: .5vw;
                                        width: 4vw; height: 4vw;
                                        // border-radius: 50%;
                                    }
                                }
                            }
                        }
                        .andsoon_tip{
                            line-height: 7vw;
                            font-size: 2.4vw;
                            text-align: left;
                            color: rgb(195, 195, 195);
                            span{
                                font-size: 2.7vw;
                                color: white;
                            }
                        }
                    }
                }
                .group_members{
                    height: 26vw;
                }
            }
            
            .delete_btn,.import_group,.clear_lineup_btn{
                right: 5vw; bottom: 5vw;left: auto;
                width: 20vw; height: 5vw;
                display: flex;
                align-items: center;
                justify-content: center;
                color: black;
                background: white;
                font-weight: bold;
            }
            .delete_btn{
                right : 30vw;
            }
            .clear_lineup_btn{
                width: 24vw;
                left: 5vw;
            }
        }
    }
}
</style>